import React from 'react'
import Icon from 'components/Icon-svg'
import {withRouter, Redirect} from 'react-router-dom'
import './index.less'
import connect from 'connect'
@connect
@withRouter
export default class extends React.Component {
    state = {
        username: '',
        username_msg: '',
        password: '',
        password_msg: ''
    }
    login = async () => {
        const {username, password} = this.state
        let username_msg, 
            password_msg
        if (!username) {
            username_msg = '请输入用户名'
        }
        if (username_msg) {
            this.setState({ username_msg })
            return
        }

        if (!password) {
            password_msg = '请输入密码'
        }
        if (password_msg) {
            this.setState({ password_msg })
            return
        }

        const {login, history, setFooterList} = this.props
        try {
            await login({
                type: 'mobile',
                mobile: 16638139714,
                password: 147258
            })
            setFooterList({title: '我的', icon: 'account1', path: '/account'})
            history.slideStatus = 'bottom'
            history.goBack()
        }catch (e) {}
        
    }
    hangleInput1 (value) {
        this.setState({
            username: value
        })
    }
    hangleInput2 (value) {
        this.setState({
            password: value
        })
    }
    render () {
        const {username_msg, password_msg} = this.state
        // if (this.props.state.user.user.name) return <Redirect to='/'/>
        return (
            <div className="login-wrapper">
               
                
            </div>
        )
    }
}



